@import "~bootstrap/scss/bootstrap.scss";

body {
  background-color: white;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.userName {
  display: flex;
  position: absolute;
  right: 0.5em;
  top: 0.5em;
  z-index: 999;
  box-shadow: 0 0.4em 0.6em rgba(0, 0, 0, 0.15);
  font-weight: bold;
  padding: 0.2em 0.5em 0.2em 0.5em;
  background: white;
  border-radius: 0.3em;
  font-size: 1em;
  border: solid #ccc;
  align-items: baseline;
}

.userCount {
  font-weight: normal;
  font-size: 0.7em;
  padding-left: 0.5em;
}
